<template>
	<mescroll-uni :ref="'mescrollRef'+i" @init="mescrollInit" height="100%" top="80" :down="downOption" @down="downCallback" :up="upOption" @up="upCallback" @emptyclick="emptyClick">
		<mescroll-empty v-if="!listData[i]"></mescroll-empty>
		<view class="" :class="{'tui-order-list':scrollTop>=0}" v-else>
			<view class="tui-order-item" v-for="(item,listIndex) in listData[i]" :key="listIndex">
				<tui-list-cell @tap="$Router.push({path:'/pages/order/integralmall/detail',query:{id:item.id}})" :hover="false" :lineLeft="false">
					<view class="tui-goods-title">
						<view>订单号：{{item.logno}}</view>
						<view class="tui-order-status" v-if="item.status==2">待发货</view>
						<view class="tui-order-status" v-if="item.status==3 && parseInt(item.time_finish)==0">已发货</view>
						<view class="tui-order-status" v-if="item.status==3 && parseInt(item.time_finish)>0">已完成</view>
					</view>
				</tui-list-cell>
				<block >
					<tui-list-cell @tap="$Router.push({path:'/pages/order/integralmall/detail',query:{id:item.id}})" padding="0"><!--@tap="$Router.push({path:'/pages/goods/detail',query:{id:goodsItem.goodsid}})"-->
						<view class="tui-goods-item">
							<image :src="item.thumb" class="tui-goods-img"></image>
							<view class="tui-goods-center">
								<view class="tui-goods-name">{{item.title}}</view>
								<view class="tui-goods-attr">{{item.optiontitleg}}</view>
							</view>
							<view class="tui-price-right">
								<view class="flex align-center">
									<view class="cu-avatar sm radius" style="background-color: #FF107F;">积</view>
									<view class="margin-left-sm">{{item.credit}}积分</view>
								</view>
								<view>x{{item.goods_num}}</view>
							</view>
						</view>
					</tui-list-cell>
				</block>
				<tui-list-cell :hover="false" unlined>
					<view class="tui-goods-price">
						<view>共{{item.goods_num}}件商品 消耗：</view>
						<view class="text-bold" style="color: #FF107F;">{{item.credit}} 积分</view>
					</view>
				</tui-list-cell>
				<view class="tui-order-btn">
					<view class="tui-btn-ml">
						<tui-button @tap="$Router.push({path:'/pages/order/integralmall/detail',query:{id:item.id}})" type="black" plain width="152rpx" height="56rpx" :size="26" shape="circle">查看详情</tui-button>
					</view>
					<view class="tui-btn-ml" v-if="item.status==3 && parseInt(item.time_finish)==0">
						<tui-button @click="takeDelivery(item)" type="danger" width="152rpx" height="56rpx" :size="26" shape="circle">确认收货</tui-button>
					</view>
				</view>
			</view>
		</view>
	</mescroll-uni>
</template>

<script>
    import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
    import MescrollMoreItemMixin from "@/components/mescroll-uni/mixins/mescroll-more-item.js";
    import tPayWay from "@/components/t-pay-way/ti-pay-way"
    export default {
        mixins: [MescrollMixin,MescrollMoreItemMixin],
        data() {
            return {
                downOption:{
                    auto:false
                },
                upOption:{
                    auto:false,
                    noMoreSize: 4,
                    empty:{
                        tip: '~ 空空如也 ~',
                        btnText: '去看看'
                    }
                },
                listData: [],
                scrollTop:0,
                payInfo: '',
                credit: 0,
                realprice: 0,
                showPayment:false,
            }
        },
		components: {
            tPayWay
        },
        props:{
            i: Number,
            index: {
                type: Number,
                default(){
                    return 0
                }
            },
            tabs: {
                type: Array,
                default(){
                    return []
                }
            }
        },
        methods: {
            /*mescrollInit(mescroll) {
                this.mescroll = mescroll;
            },*/
            getList:function () {
                let params = {page:this.mescroll.num,pagesize:this.mescroll.size}
				if(this.index>0) params.status = this.index + 1
                this.$http.get('creditshop.log.getlist',params).then((res)=>{
                    this.mescroll.endBySize(this.mescroll.size, parseInt(res.total));
                    if(this.mescroll.num == 1) this.listData[this.index] = [];
                    //this.listData = this.listData.concat(res.list)
					if(res.list) this.$set(this.listData,this.index,this.listData[this.index].concat(res.list))

                })
            },
            downCallback:function () {
                this.mescroll.resetUpScroll()
                this.getList()
            },
            upCallback() {
                this.getList()
            },
            actionPay:function (orderid) {
                this.$http.get('order.pay',{id:orderid}).then((res)=>{
                    this.payInfo = res
                    this.credit = parseInt(res.credit.current)
                    this.showPayment = true
                })
            },
            paymentClose:function () {
                this.$utils.modal.confirm('确定取消支付吗？').then((res)=>{
                    this.showPayment = false
                })
            },
            payment:function (params) {
                switch (params.type){
                    case 'credit':
                        this.$http.get('order.pay.complete',{id:this.payInfo.order.id,type:params.type}).then((res)=>{
                            if(res.error==0){
                                this.showPayment = false
								this.downCallback()
                                //this.$Router.push({path:'/pages/order/paySuccess',query:{orderid:this.payInfo.order.id}})
                            }else{
                                this.$utils.toast('支付出错')
                            }
                        })
                        break;
                    default:
                        this.$utils.toast('请先选择支付方式')
                        return;
                        break;
                }
            },
            takeDelivery:function (item) {
                this.$utils.modal.confirm('确定完成收货吗？').then((res)=>{
                    this.$http.get('creditshop.log.finish',{id:item.id}).then((res)=>{
						if(res.error==0){
                            this.$utils.toast('收货成功')
							this.downCallback()
						}
                    })
                })
            },
            navTo:function (url,params) {
                params = params||{}
                let tabArr = ['/pages/index/index','/pages/tabbar/integralmall','/pages/tabbar/luckybag','/pages/tabbar/cart','/pages/member/index']
                if(tabArr.indexOf(url)>=0) this.$Router.pushTab({path:url})
                else this.$Router.push({path:url,query:params})
            },
        },
        onPageScroll(e) {
            this.scrollTop = e.scrollTop;
        }
    }
</script>
